.header .usuario {
  height: 6rem;
  width: 6rem;
  position: fixed;
  color: black;
  right: 2%;
  top: 0%;
}
.header .usuario ion-icon {
  color: #306cc0;
  height: 6rem;
  width: 6rem;
}
.header ul, .header ol {
  list-style: none;
}
.header .nav > li {
  float: left;
}
.header .nav li a {
  background-color: #1C4176;
  color: #FFFFFF;
  text-decoration: none;
  padding: 0 1rem;
  display: block;
}
.header .nav li a:hover {
  background-color: #10233d;
}
.header .nav li ul {
  display: none;
  position: absolute;
  min-width: 14rem;
}
.header .nav li:hover > ul {
  display: block;
}
.header .nav li ul li {
  position: relative;
}
.header .nav li ul li ul {
  right: -140px;
  top: 0px;
}